<script lang="ts">
  import { createEventDispatcher, onDestroy, onMount } from "svelte"
  import { Analysis } from "@lib/webar"
  import imageSource from "../best-match/stores/image-source"

  const dispatch = createEventDispatcher<{
    hasFace: boolean
    hasLight: boolean
  }>()

  export const takePhoto = () => Analysis.useWebcam().then((wcam) => wcam.takePhoto())

  let webar: HTMLElement

  /** defines if Analysis is loaded */
  let loaded = false

  // sync Analysis image source with the state's one
  $: $imageSource && $imageSource.type === "webcam" && loaded && Analysis.useWebcam()

  $: loaded && Analysis.render(webar)

  const hasFaceEventListener = (res: boolean) => dispatch("hasFace", res)
  const hasLightEventListener = (res: boolean) => dispatch("hasLight", res)

  onMount(() => {
    Analysis.addEventListener("face", hasFaceEventListener)
    Analysis.addEventListener("lightning", hasLightEventListener)
    Analysis.start().then(() => (loaded = true))
  })
  onDestroy(() => {
    Analysis.unmount(webar)
    Analysis.removeEventListener("face", hasFaceEventListener)
    Analysis.removeEventListener("lightning", hasLightEventListener)
  })
</script>

<div bind:this={webar} />

<style lang="postcss">
  div,
  div > :global(canvas) {
    @apply h-full w-full;
  }
</style>
