import { timers } from "@banuba/webar"
import type { BestColors } from "@lib/sca/best-colors"
import { random } from "@lib/utils/lodash"
import { toBase64 } from "./utils"

export type HexColor = string
export type ImageUrl = string

export enum AnalysisStep {
  SkinType = 0,
  SkinTone = 1,
  ColorType = 2,
  FacialFeatures = 3,
}

export type AnalysisProgress = {
  percent: number
  step: AnalysisStep
}

type ProgressCallback = (progress: AnalysisProgress) => void

export interface CreamColors {
  bb_colors: string[]
  cc_colors: string[]
}
export interface CreamColorsRanges {
  BB: number[]
  CC: number[]
}

export enum Season {
  SPRING = 0,
  SUMMER = 1,
  AUTUMN = 2,
  WINTER = 3,
}

export type AnalysisResult = {
  season: keyof typeof Season
  title: string
  skinTones: HexColor[]
  hairTones: HexColor[]
  eyeTones: HexColor[]
  tonesDescription: string
  celebrities: string[]
  bestColors: BestColors
  colorsDescription: string
  appearances: ImageUrl[]
  creamRanges?: CreamColorsRanges
}

type SeasonColorAnalysisResponse = [
  SpringProbability: number,
  SummerProbability: number,
  AutumnProbability: number,
  WinterProbability: number
]

const creamColorRangesEndpoint = `${import.meta.env.VITE_SCA_ENDPOINT}bb-cc-recommender-v2`

export const analyze = async (
  image: Blob,
  onProgress: ProgressCallback = () => {}
): Promise<AnalysisResult> => {
  const [probabilities, Seasons, creamRanges] = await Promise.all([
    fetch(import.meta.env.VITE_SCA_ENDPOINT, {
      method: "POST",
      body: JSON.stringify({
        isBase64Encoded: true,
        body: await toBase64(image),
      }),
    }).then((r) => r.json() as Promise<SeasonColorAnalysisResponse>),

    import("./seasons").then((m) => m.default),

    fetch(creamColorRangesEndpoint, {
      method: "POST",
      body: JSON.stringify({
        isBase64Encoded: true,
        body: await toBase64(image),
      }),
    }).then((r) => r.json() as Promise<CreamColorsRanges>),

    fakeProgress(onProgress),
  ])

  const index = probabilities.indexOf(Math.max(...probabilities))
  const season = Season[index] as keyof typeof Season

  const description = Seasons[season]

  return {
    season,
    ...description,
    creamRanges,
  }
}

export const fakeProgress = async (onProgress: ProgressCallback = () => {}) => {
  const duration = random(3.8 * 1000, 4.8 * 1000)
  const durationByStep = {
    [AnalysisStep.SkinType]: duration * 0.25,
    [AnalysisStep.SkinTone]: duration * 0.19,
    [AnalysisStep.ColorType]: duration * 0.22,
    [AnalysisStep.FacialFeatures]: duration * 0.34,
  }

  let percent = 0

  // processing simulation
  for (const [step, duration] of Object.entries(durationByStep)) {
    const range = 100 / Object.keys(durationByStep).length

    onProgress({ percent, step: +step })

    await delay(300)

    for await (const _ of ticks(range, duration)) onProgress({ percent: ++percent, step: +step })
  }
}

const delay = (t: number) => new Promise<void>((r) => timers.setTimeout(r, t))

async function* ticks(count: number, duration: number) {
  const dt = duration / count

  while (count-- > 0) yield await delay(dt)
}
