<script context="module" lang="ts">
  const titles = ["Skin type", "Skin tone", "Color type", "Facial features"]
</script>

<script lang="ts">
  import { createEventDispatcher } from "svelte"
  import { fade } from "svelte/transition"
  import { Checkmark, Icon, Progress, Transition } from "@lib/elements"
  import CheckmarkLg from "@lib/elements/Icon/svg/checkmark-lg.svg"
  import { completed, percent, step } from "./state"

  export let intro = false
  export let isGift: boolean = false

  const dispatch = createEventDispatcher<{ ended: void }>()

  let current = $step
  let finished = $completed

  $: title = titles[current]
  $: done = current < $step || $completed

  const move = () => (current = $step)

  $: done && setTimeout(move, 600) // let user to see the step completion status
  $: $completed && setTimeout(() => (finished = true), 600) // let user to see the last step completion status
</script>

<section class="tint-face-analysis">
  {#if finished}
    <article
      in:fade|local={{ delay: 400, duration: 800 }}
      on:introend={() => setTimeout(() => dispatch("ended"), 600)}
    >
      <h2>
        <Checkmark size="lg" active />
        {#if isGift}
          Gift selection finished.
        {:else}
          Analysis finished.
        {/if}
      </h2>
      <p>Personalized product recommendations are ready.</p>
    </article>
  {:else}
    <Transition {intro}>
      <div in:fade|local={{ delay: 100, duration: 600 }} out:fade={{ duration: 150 }}>
        <!-- svelte-ignore a11y-label-has-associated-control : it's ok to wrap <progress> with <label> -->
        <label>
          <span>{`${isGift ? "Gift selection" : "Analyzing"} ${$percent}%`}</span>
          <Progress value={$percent} chunks={titles.length} />
        </label>
        <p class:completed={done}>
          <Icon src={CheckmarkLg} />
          {title}...
        </p>
      </div>
    </Transition>
  {/if}
</section>

<style lang="postcss">
  section {
    @apply relative h-full w-full;
    @apply xl:max-h-[48.5rem] xl:max-w-[75rem] xl:rounded-xl;

    & > :global(*) {
      @apply absolute top-1/2 left-1/2 -translate-x-1/2 -translate-y-1/2;
    }
  }

  article {
    @apply relative flex shrink-0 flex-col items-center;
  }

  h2 {
    @apply absolute -top-2 flex -translate-y-full flex-col items-center gap-4 text-xl md:gap-5 md:text-2xl;
  }

  div {
    @apply flex shrink-0 flex-col gap-7 text-center;
  }

  label {
    @apply flex flex-col gap-3 text-xl font-bold md:gap-4 md:text-2xl;
    @apply w-[19.375rem] md:w-[21rem] lg:w-[47.5rem];
  }

  label + p {
    @apply flex justify-center text-base;

    & > :global(:first-child) {
      @apply mr-1 text-[#4ccc75] transition-opacity;
    }

    &:not(.completed) > :global(:first-child) {
      @apply opacity-0 transition-none;
    }

    & :global(path) {
      @apply stroke-current;
    }
  }
</style>
