<script context="module" lang="ts">
  const clamp = (x: number, min: number, max: number) => Math.min(Math.max(min, x), max)
</script>

<script lang="ts">
  export let max = 100
  export let value = max / 2
  export let chunks = 1
</script>

<!-- Styling of native HTMLProgressElement is miserable -->
<div
  class="tint-progress"
  role="progressbar"
  aria-valuemax={max}
  aria-valuenow={value}
  {...$$restProps}
>
  {#each new Array(chunks) as _, idx}
    {@const relativeMax = max / chunks}
    {@const relativeValue = clamp(((value - idx * relativeMax) / relativeMax) * 100, 0, max)}
    <div class="chunk" style={`--value: ${relativeValue}`} />
  {/each}
</div>

<style lang="postcss">
  .tint-progress {
    @apply inline-flex h-2 w-full gap-1 rounded lg:gap-2;
  }

  .chunk {
    @apply relative h-full w-full rounded-[inherit] bg-[#dcdcdd];

    &::after {
      @apply absolute top-0 left-0 h-full w-[calc(var(--value)*1%)] rounded-[inherit] bg-[#4ccc75] transition-[width] content-[""];
    }
  }
</style>
