<script context="module" lang="ts">
  import { readable } from "svelte/store"

  const createCountdown = (time: number) =>
    readable(time, (set) => {
      const it = setInterval(() => {
        set((time -= 1))
        if (time <= 0) clearInterval(it)
      }, 1000)

      return () => clearInterval(it)
    })
</script>

<script lang="ts">
  import { createEventDispatcher } from "svelte"
  import { Icon, Popup } from "@lib/elements"
  import Viewer from "./Viewer.svelte"

  const dispatch = createEventDispatcher<{ photo: Blob }>()

  let viewer: Viewer
  let countdown: ReturnType<typeof createCountdown> | null = null
  let flash = false

  $: hasFace = false
  $: hasLight = false
  $: hint = hasFace ? "Please, donâ€™t move" : "Put your face in the frame"

  $: flash && viewer.takePhoto().then((photo) => dispatch("photo", photo))
  $: hasFace ? (countdown = createCountdown(3)) : (countdown = null)
</script>

<section
  class:flash={countdown && $countdown === 0}
  class="tint-face-analysis"
  on:transitionend={() => (flash = true)}
>
  <div class="viewer" class:hasLight class:hasFace>
    <Viewer
      bind:this={viewer}
      on:hasFace={({ detail }) => (hasFace = detail)}
      on:hasLight={({ detail }) => (hasLight = detail)}
    />
    {#if countdown}<span class="countdown">{$countdown}</span>{/if}
    <span class="hint">{hint}</span>

    <dialog class:hasLight class:hasFace>
      <Icon variant="info-circle" />
      <p>Not enough light</p>
    </dialog>
  </div>
  <article>
    <h2>Face analysis</h2>
    <p>We will analyze your face and suggest personalized products</p>
  </article>
</section>

