<script context="module" lang="ts">
  type Size = "auto" | "xs" | "lg"

  export type { Size }
</script>

<script lang="ts">
  import lg from "../Icon/svg/checkmark-lg.svg"
  import md from "../Icon/svg/checkmark-md.svg"
  import sm from "../Icon/svg/checkmark-sm.svg"
  import xs from "../Icon/svg/checkmark-xs.svg"
  import { cx } from "../utils"

  export let active = false
  export let size: Size = "auto"

  $: classes = cx("tint-checkmark", $$restProps.class, size, active && "active")

  // FIXME: currently it's not possible to use url('./checkmark.svg') inside css due to vite.config.js `emitCss: false` setting
  let container: HTMLElement
  $: container &&
    (container.style.setProperty("--icon-xs", `url("${xs}")`),
    container.style.setProperty("--icon-sm", `url("${sm}")`),
    container.style.setProperty("--icon-md", `url("${md}")`),
    container.style.setProperty("--icon-lg", `url("${lg}")`))
</script>

<i class={classes} bind:this={container} />

<style lang="postcss">
  i {
    @apply inline-block h-7 w-7 rounded-full bg-[#dcdcdd] bg-[image:var(--icon-sm)] bg-center bg-no-repeat transition-colors;
    @apply md:h-8 md:w-8 md:bg-[image:var(--icon-md)] lg:h-[2.625rem] lg:w-[2.625rem] lg:bg-[image:var(--icon-lg)];
  }

  .xs {
    @apply h-5 w-5 bg-[image:var(--icon-xs)];
  }

  .lg {
    @apply h-[2.625rem] w-[2.625rem] bg-[image:var(--icon-lg)];
  }

  .active {
    @apply bg-[#4ccc75];
  }
</style>
